import React, {Component} from 'react';
import {Transition} from 'react-spring';

import './Select.scss';
import {COLORS} from "../../../variables";

class Select extends Component {

	constructor() {
		super();
		this.state = {
			activeOption: null,
			show: false
		};
	}


	toggleDropdown = () => {
		this.setState(state => ({show: !this.state.show}));
		//this.vitaEntry
	};

	doShite = (id, callback) => {
		this.toggleDropdown();
		this.setState(state => ({activeOption: id}));
		callback(id);
	};


	render() {
		const {callback, options, style} = this.props;
		/*
				return (
					<div className="dropdown">
						<div className="option active"
							 onClick={this.toggleDropdown}>{this.state.activeOption ? this.state.activeOption : "Select Grid Size"}</div>
						<Trail
							native
							items={options}
							keys = {item => item.id}
							from={{top: 0}}
							to={{top: this.state.show ? 0 : 100}}>
							{item => props => (
								<animated.div className="option"
									 onClick={() => this.doShite(item.value, callback)}
									// ref={el => this.option = el}
									 style={props}>{item.label}</animated.div>
							)}
						</Trail>
					</div>
				);
		*/
		return (
			<div className="dropdownWrapper">
				<div className="dropdown">
					<div className="option active"
						 onClick={this.toggleDropdown}>{this.state.activeOption ? this.state.activeOption : "Select Grid Size"}</div>


					<Transition
						items={this.state.show}
						from={{opacity: 0}}
						enter={{opacity: 1}}
						leave={{opacity: 0}}>
						{(show) =>
							show && (props => {
								return (
									options.map(item =>
										<div className="option"
											 key={item.id}
											 onClick={() => this.doShite(item.value, callback)}
											 ref={el => this.option = el}
											 style={props}>{item.label}</div>
									)
								)
							})
						}
					</Transition>
				</div>
			</div>
		);
	}
}

export default Select;
