import React, {Component} from 'react';
import AppContext from "../../../App-context";

class VitaEntryContent extends Component {

	render() {
		const {id, duration, employer, activity, misc} = this.props.item;

		return (
			<div className="vitaContent">
				{duration && <p className="duration">{duration}</p>}
				{employer && <p className="employer">{employer}</p>}
				{activity && <p className="activity">{activity}</p>}
				{misc && <p className="misc">{misc}</p>}
			</div>
		);
	}
}

VitaEntryContent.contextType = AppContext;

export default VitaEntryContent;
